@font-face {
    font-family: 'UIRegular';
    font-display: swap;
    src: url(../../static/fonts/Regular.d41b997.woff2) format('woff2'), url(../../static/fonts/Regular.b977f73.otf) format('opentype');
    font-weight: 200
}

@font-face {
    font-family: 'UISemiBold';
    font-display: swap;
    src: url(../../static/fonts/SemiBold.f51db15.woff2) format('woff2'), url(../../static/fonts/SemiBold.ae1f2da.otf) format('opentype');
    font-weight: 300
}

body {
    font-family: UIRegular, SemiBold, Arial, sans-serif !important
}

html body {
    font-family: UIRegular, SemiBold, Arial, sans-serif !important
}

/*----- reset css -----*/

body,
dl,
dd,
ul,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
p,
fieldset,
input,
textarea,
blockquote {
    margin: 0;
}

ul,
ol,
input,
textarea,
fieldset,
th,
td {
    padding: 0;
}

fieldset,
img,
abbr,
acronym {
    border: 0;
}

ol,
ul {
    list-style: none;
}

input,
textarea {
    border-width: 1px;
    outline: none;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var,
i {
    font-weight: normal;
    font-style: normal;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: normal;
    font-size: 100%;
}

q:before,
q:after {
    content: "";
}

img {
    vertical-align: top;
}

caption,
th {
    text-align: center;
}

a {
    color: #fff;
    text-decoration: none;
    cursor: pointer;
}

table {
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    font-variant: inherit;
    color: inherit;
}

:focus {
    outline: none;
}

.header {
    width: 100%;
    position: relative;
}

.pull-left {
    float: left !important;
}

.pull-right {
    float: right !important;
}

.clearfix:after {
    content: "";
    clear: both;
    display: block;
    width: 0;
    height: 0;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    transition-delay: 99999s;
    -webkit-transition-delay: 99999s;
    transition: color 99999s ease-out, background-color 99999s ease-out;
    -webkit-transition: color 99999s ease-out, background-color 99999s ease-out;
}

.hide {
    display: none !important;
}

.fade-out {
    -webkit-transition-duration: 400ms;
            transition-duration: 400ms;
    opacity: 0;
}

.db {
    display: block;
}

.show {
    display: block !important;
}

.vhide {
    visibility: hidden;
}

.text-overflow {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.text-nowrap {
    white-space: nowrap;
}

.text-breakall {
    word-break: break-all;
}

.ovh {
    overflow: hidden;
}

.tr {
    text-align: right;
}

.tl {
    text-align: left;
}

.tc {
    text-align: center;
}

.hide-input {
    position: absolute;
    width: 0;
    height: 0;
    border: 0px none;
}

html ::-webkit-scrollbar {
    width: 3px;
    height: 5px;
}

html ::-webkit-scrollbar,
html ::-webkit-scrollbar-track {
    background-color: #182033;
}

html ::-webkit-scrollbar-thumb {
    border-radius: 4px;
}

html ::-webkit-scrollbar-thumb {
    width: 3px;
    height: 5px;
    background-color: rgba(128, 144, 165, 0.5);
}

html::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    background-color: transparent;
}

html::-webkit-scrollbar-track {
    background: #182033;
    border-radius: 15px;
}

html::-webkit-scrollbar-thumb {
    background-color: #6d758d;
    border-radius: 15px;
    width: 5px;
    cursor: pointer;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

html::-webkit-scrollbar-thumb:hover {
    background-color: #888caa;
}

/*----------- reset End----------*/

.topprice {
    max-width: 78%;
}

.topprice li {
    float: left;
    padding-left: 20px;
    height: 30px;
    line-height: 30px;
    color: #ffffff;
}

.login_lan li {
    line-height: 30px;
    margin-left: 40px;
    float: left;
}

.top-links {
    position: relative;
}

.login_lan img {
    vertical-align: middle;
}

.add-dn-qr {
    position: absolute;
    top: 100%;
    left: -100px;
    z-index: 9999;
    padding: 5px 15px 5px;
    background: #f5f5f5;
    -webkit-box-shadow: 0 10px 8px rgba(0, 0, 0, 0.2);
            box-shadow: 0 10px 8px rgba(0, 0, 0, 0.2);
    color: #d45858;
    display: none;
}

.top-links:hover .add-dn-qr {
    display: block;
}

.login_lan .lang-option {
    color: #ddd;
    padding: 0 25px 0 11px;
    position: relative;
    cursor: default;
}

.lang-option span {
    display: inline-block;
    vertical-align: middle;
    margin-top: -3px;
}

.login_lan .more-lan {
    position: absolute;
    top: 100%;
    right: 0;
    z-index: 9998;
    display: none;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

.login_lan .more-lan a {
    background: #1b1c25;
    padding: 10px;
    line-height: 1;
    display: block;
}

.lang-option .iti-flag {
    margin-right: 5px;
}

.login_lan .more-lan a:hover {
    background-color: #232830;
}

.login_lan .lang-option:hover .more-lan {
    display: block;
}

.trade_login_lan .style-box,
.trade_login_lan.login_lan .lang-option {
    color: #fff;
}

.trade_login_lan.login_lan li {
    margin-right: 10px;
    margin-left: 30px;
    padding-top: 15px;
    padding-bottom: 15px;
}

.trade_login_lan .more-lan:hover {
    background-color: transparent;
}

/*----------- copy End --------------*/

/* Styled scrollbars */

.iScrollHorizontalScrollbar {
    position: absolute;
    z-index: 9999;
    height: 16px;
    left: 2px;
    right: 2px;
    bottom: 2px;
    overflow: hidden;
}

.iScrollHorizontalScrollbar.iScrollBothScrollbars {
    right: 18px;
}

.iScrollVerticalScrollbar {
    position: absolute;
    z-index: 9999;
    width: 3px;
    bottom: 2px;
    top: 2px;
    right: 0;
    overflow: hidden;
}

.iScrollVerticalScrollbar.iScrollBothScrollbars {
    bottom: 18px;
}

.iScrollIndicator {
    position: absolute;
    background: #292e38;
    border-radius: 8px;
    width: 100%;
}

